.pageLoader-screen {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .farm-bit {
    color: #0d3f3b;
  }
  .farm-bit-btn {
    color: #fff;
    font-size: 14px;
  }
  .smarter-bit-btn {
    color: #768d36;
    font-size: 14px;
  }
  .smarter-bit {
    color: #768d36;
  }
  .farm-text-amin {
    animation: glow 2s linear 0s infinite forwards;
  }
  @keyframes glow {
    0% {
      width: 0%;
      overflow: hidden;
      filter: drop-shadow(0 0 0px #0d3f3b);
    }
    100% {
      width: 115%;
      overflow: hidden;
      filter: drop-shadow(0 0 25px #768d36);
    }
  }
  .line-under-loader {
    width: 100px;
    height: 3px;
    background-color: #0d3f3b;
    animation: slide 2s linear 0s infinite forwards;
  }
  @keyframes slide {
    0% {
      margin-left: -100px;
      overflow: hidden;
      background-color: #0d3f3b;
      filter: drop-shadow(0 0 0px #0d3f3b);
    }
    100% {
      margin-left: 100px;
      background-color: #768d36;
      overflow: hidden;
      filter: drop-shadow(0 0 25px #768d36);
    }
  }